<template>
  <div class="Login-box">
    <div class="Login-img">
      <img
        src="https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/2019pILfAg7Avr1567732916.png"
        alt=""
      />
    </div>
    <van-form @submit="onSubmit">
      <div class="lwh_input" v-for="(item, index) in lwh_value" :key="index">
        <input
          :class="{ lwh_valueIndex: index == lwh_valueIndex }"
          :type="item.txt"
          v-model="item.cont"
          :placeholder="item.place"
          @focus="lwh_focus(index)"
          @blur="lwh_blur()"
        />
        <span v-if="index == 0 && lwh_flag" @click="lwh_span_veify()"
          >获取验证码</span
        >
        <p v-if="index == 0 && !lwh_flag">获取验证码({{ lwh_time }})</p>
      </div>
      <div class="vnt-text">
        <span>*未注册的手机号将自动注册</span>
        <span @click="add">密码登录</span>
      </div>

      <div style="margin: 16px">
        <van-button
          round
          block
          type="info"
          native-type="submit"
          color="linear-gradient(to right, rgb(255, 146, 71),rgb(253, 88, 3))"
          @click="lwh_login"
        >
          登录
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      lwh_value: [
        { cont: "", txt: "text", type: "name", place: "请输入手机号" },
        { cont: "", txt: "text", type: "pass", place: "请输入短信验证码" },
      ],
      lwh_valueIndex: null,
      lwh_flag: true,
      lwh_time: 59,
      acs:null,
    };
  },
  methods: {
    onSubmit(values) {
      console.log("submit", values);
    },
    add() {
      this.$router.push({
        path: "/Wyl-Login",
      });
    },
    // 获取焦点
    lwh_focus(index) {
      this.lwh_valueIndex = index;
    },
    // 失去焦点
    lwh_blur() {
      this.lwh_valueIndex = null;
    },
    // 获取验证码
    async lwh_span_veify() {
      if (/^1[3456789]d{9}$/.test(this.lwh_value[0].cont)) {
        Toast("手机号格式错误");
        return false;
      }

      this.acs = setInterval(() => {
        this.lwh_time--;
        if (this.lwh_time == 0) {
          this.lwh_time = 59;
          this.lwh_flag = !this.lwh_flag;
          clearInterval(this.acs);
        }
      }, 1000);

      this.lwh_flag = !this.lwh_flag;
      let { data } = await this.$http.post("/api/app/smsCode", {
        mobile: this.lwh_value[0].cont,
        sms_type: "login",
      });
      if (data.code != 200) return Toast(data.msg);
      console.log(data);
    },
    // 注册登录
    async lwh_login() {
      let { data } = await this.$http.post("/api/app/login", {
        mobile: this.lwh_value[0].cont,
        sms_code: this.lwh_value[1].cont,
        client: 1,
        type: 2,
      });

      if (data.code != 200) return Toast(data.msg);

      if (data.data.is_new == 1) {
        Toast(data.msg);
        this.$router.push("/lwh_pass");
        this.$store.commit("lwh_saveBtn", {
          type: "昵称",
          value: data.data.nickname,
        });
      } else {
        Toast(data.msg);
        this.$router.push({
          path: "/lwh_my",
          query: "nickname",
        });
      }
      window.sessionStorage.setItem("token", data.data.remember_token);
    },
  },
};
</script>

<style scoped lang="scss">
.Login-box {
  width: 100vw;
  height: 100vh;
}
.Login-img {
  width: 100%;
  height: 1.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Login-img img {
  width: 2.3rem;
  height: 0.6rem;
}

.vnt-text {
  width: 80vw;
  font-size: 0.15rem;
  color: gray;
  display: flex;
  justify-content: space-between;
  text-align: center;
  margin-left: 0.4rem;
  margin-top: 0.3rem;
}
.van-button {
  width: 70vw;
  margin-top: 0.6rem;
  margin-left: 0.4rem;
}
.van-field {
  width: 85vw;
  text-align: center;
  margin-left: 0.3rem;
}
.van-button {
  width: 70vw;
  margin-top: 0.6rem;
  margin-left: 0.4rem;
}
.lwh_input {
  width: 3.2rem;
  height: 0.52rem;
  margin-left: 0.41rem;
  margin-top: 0.02rem;
  position: relative;
  > input {
    width: 100%;
    border: none;
    border-bottom: 0.01rem solid #f1f1f1;
    height: 100%;
  }
  > .lwh_valueIndex {
    border-bottom: 0.01rem solid #eb6100;
  }
  > input::placeholder {
    color: #b7b7b7;
  }
  > span {
    font-size: 0.13rem;
    color: #eb6100;
    position: absolute;
    top: 0.17rem;
    right: 0.05rem;
  }
  > p {
    font-size: 0.13rem;
    color: rgb(204, 204, 204);
    position: absolute;
    top: 0.17rem;
    right: 0.05rem;
  }
}
</style>